.definitions {
   display: flex; 
   flex-direction: column;
   overflow-y:auto;
   overflow-x: hidden;
   scrollbar-width: none;
   height: 55vh;
   border: 10px solid rgb(105, 105, 105);
   border-radius: 10px;
   padding: 10px 20px;
}

/*
.definitions::-webkit-scrollbar{    
   height: 20px;
   width: 12px;
   background: #000;
}
*/
.subTitle {
    font-size: 5vw;
    font-family: 'Montserrat', sans-serif;
}

.explanation {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}

@media (max-width: 900px) {
    .meanings {
        height: 40vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}